export default {
	data() {
		return {
			uid: '',
			list: ''
		};
	},
	methods:{
		call(mobile){
			uni.makePhoneCall({
					phoneNumber: `${mobile}`
			});
		}
	},
	onShow(){
		this.$store.dispatch('teamInfo',{
			uid: this.uid
		}).then(res=>{
			this.list = res.data
		}).catch(err=>{})
	},
	onLoad(e){
		this.uid = e.id
	}
}
